
.swiper-container {
  margin: 50px 160px 0;
  -webkit-transition: 5s;
  transition: 5s;
}
.swiper-slide {
  height: 250px;
}
.swiper-slide img {
    width: 100%;
    height: 100%;
}
.swiper-slide .script {
    height: 50px;
    line-height: 50px;
    background: rgba(0, 0, 0, 0.4);
    position: absolute;
    left: 0;
    bottom: 0;
    right: 0;
    text-align: center;
}
.swiper-slide .script span {
      vertical-align: middle;
      font-size: 14px;
}
.tips {
  text-align: center;
  font-size: 16px;
  margin-top: 80px;
  -webkit-transition: 1s;
  transition: 1s;
}
@media screen and (max-width: 320px) {
.swiper-container {
    margin: 20px 5px 0;
}
.swiper-container .swiper-slide {
      height: 100px;
}
.swiper-container .script {
      height: 35px;
      line-height: 10px;
}
.swiper-container .script span {
        font-size: 10px;
}
.tips {
    font-size: 14px;
    margin-top: 30px;
}
}
@media screen and (min-width: 321px) and (max-width: 460px) {
.swiper-container {
    margin: 30px 10px 0;
}
.swiper-container .swiper-slide {
      height: 150px;
}
.swiper-container .script {
      height: 45px;
      line-height: 12px;
}
.swiper-container .script span {
        font-size: 11px;
}
.tips {
    font-size: 15px;
    margin-top: 40px;
}
}
@media screen and (min-width: 461px) and (max-width: 640px) {
.swiper-container {
    margin: 30px 10px 0;
}
.swiper-container .swiper-slide {
      height: 150px;
}
.swiper-container .script {
      height: 40px;
      line-height: 12px;
}
.swiper-container .script span {
        font-size: 12px;
}
.tips {
    font-size: 15px;
    margin-top: 40px;
}
}
@media screen and (min-width: 641px) and (max-width: 900px) {
.swiper-container {
    margin: 40px 30px 0;
}
.swiper-container .swiper-slide {
      height: 200px;
}
.swiper-container .script {
      height: 40px;
      line-height: 14px;
}
.swiper-container .script span {
        font-size: 14px;
}
.tips {
    font-size: 16px;
    margin-top: 50px;
}
}
@media screen and (min-width: 901px) and (max-width: 1200px) {
.swiper-container {
    margin: 50px 40px 0;
}
.swiper-container .swiper-slide {
      height: 220px;
}
.swiper-container .script {
      height: 45px;
      line-height: 20px;
}
.swiper-container .script span {
        font-size: 14px;
}
.tips {
    font-size: 16px;
    margin-top: 50px;
}
}



