
.section {
  overflow: hidden;
}
.about-me .info-list ul {
  width: 50%;
  margin: 70px auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.about-me .info-list ul .info-item {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    text-align: center;
}
.about-me .info-list ul .info-item:nth-of-type(1) {
      -webkit-transition: 1s 0.4s;
      transition: 1s 0.4s;
}
.about-me .info-list ul .info-item:nth-of-type(2) {
      -webkit-transition: 1s 0.8s;
      transition: 1s 0.8s;
}
.about-me .info-list ul .info-item:nth-of-type(3) {
      -webkit-transition: 1s 1.2s;
      transition: 1s 1.2s;
}
.about-me .info-list ul .info-item:nth-of-type(4) {
      -webkit-transition: 1s 1.6s;
      transition: 1s 1.6s;
}
.about-me .info-list ul .info-item img {
      width: 60px;
      height: 60px;
      -webkit-transition: 500ms;
      transition: 500ms;
}
.about-me .info-list ul .info-item span {
      display: inline-block;
      margin-top: 20px;
}
.icon-enter-active, .icon-leave-active {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}
.icon-leave-active {
  -webkit-transition: 1s 0s !important;
  transition: 1s 0s !important;
}
.icon-enter, .icon-leave-to {
  opacity: 0;
  -webkit-transform: translateX(-280px);
          transform: translateX(-280px);
}
.icon-scale {
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
}
.info-text {
  width: 50%;
  margin: 0 auto;
}
.info-text .text-content {
    text-indent: 35px;
    line-height: 40px;
}
.info-text .text-content.text-enter-active, .info-text .text-content.text-leave-active {
      -webkit-transition: 1s 2s;
      transition: 1s 2s;
      -webkit-transform: translateY(0);
              transform: translateY(0);
}
.info-text .text-content.text-leave-active {
      -webkit-transition: 1s;
      transition: 1s;
}
.info-text .text-content.text-enter, .info-text .text-content.text-leave-to {
      opacity: 0;
      -webkit-transform: translateY(200px);
              transform: translateY(200px);
}
@media screen and (max-width: 460px) {
.about-me .info-list ul {
    width: 90%;
    margin: 30px auto;
}
.about-me .info-list ul .info-item img {
      width: 40px;
      height: 40px;
}
.about-me .info-list ul .info-item span {
      font-size: 13px;
      margin-top: 15px;
}
.info-text {
    width: 90%;
}
.info-text .text-content {
      font-size: 14px;
      line-height: 23px;
}
.icon-enter, .icon-leave-to {
    -webkit-transform: translateX(-100px);
            transform: translateX(-100px);
}
}
@media screen and (min-width: 461px) and (max-width: 640px) {
.about-me .info-list ul {
    width: 80%;
    margin: 33px auto;
}
.about-me .info-list ul .info-item img {
      width: 45px;
      height: 45px;
}
.about-me .info-list ul .info-item span {
      font-size: 16px;
      margin-top: 16px;
}
.info-text {
    width: 80%;
}
.info-text .text-content {
      font-size: 15px;
      line-height: 26px;
}
.icon-enter, .icon-leave-to {
    -webkit-transform: translateX(-120px);
            transform: translateX(-120px);
}
}
@media screen and (min-width: 641px) and (max-width: 900px) {
.about-me .info-list ul {
    width: 70%;
    margin: 33px auto;
}
.about-me .info-list ul .info-item img {
      width: 50px;
      height: 50px;
}
.about-me .info-list ul .info-item span {
      font-size: 18px;
      margin-top: 20px;
}
.info-text {
    width: 70%;
}
.info-text .text-content {
      font-size: 16px;
      line-height: 35px;
}
.icon-enter, .icon-leave-to {
    -webkit-transform: translateX(-160px);
            transform: translateX(-160px);
}
}
@media screen and (min-width: 901px) and (max-width: 1200px) {
.about-me .info-list ul {
    width: 60%;
}
.info-text {
    width: 60%;
}
.icon-enter, .icon-leave-to {
    -webkit-transform: translateX(-180px);
            transform: translateX(-180px);
}
}
@media screen and (min-width: 1201px) and (max-width: 1500px) {
.icon-enter, .icon-leave-to {
    -webkit-transform: translateX(-210px);
            transform: translateX(-210px);
}
}



