
@charset "UTF-8";
.motto, .text {
  width: 500px;
  margin: 0 auto;
  margin-top: 40px;
}
.motto ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.motto ul .motto-item {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    text-align: center;
    font-size: 24px;
}
.motto ul .motto-item:nth-of-type(1) {
      color: salmon;
}
.motto ul .motto-item:nth-of-type(2) {
      color: green;
}
.motto ul .motto-item:nth-of-type(3) {
      color: orange;
}
.motto ul .motto-item:nth-of-type(4) {
      color: blue;
}
.left-in-enter-active, .left-in-move-active {
  -webkit-transition: 1s;
  transition: 1s;
  -webkit-transform: translateX(0);
          transform: translateX(0);
}
.left-in-enter, .left-in-leave-to {
  -webkit-transform: translateX(-400px);
          transform: translateX(-400px);
  opacity: 0;
}
.text .text-item {
  text-align: center;
  font-size: 20px;
  margin: 15px 0;
}
.right-in-enter-active, .right-in-leave-active {
  -webkit-transition: 1s;
  transition: 1s;
  -webkit-transform: translateX(0);
          transform: translateX(0);
}
.right-in-enter, .right-in-leave-to {
  -webkit-transform: translateX(400px);
          transform: translateX(400px);
  opacity: 0;
}
.contact {
  margin: 50px auto 0;
  width: 300px;
}
.contact ul {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
.contact ul .contact-item {
      width: 40px;
      height: 40px;
      text-align: center;
      position: relative;
}
.contact ul .contact-item:nth-of-type(1) {
        -webkit-transition: 0.7s 0.4s;
        transition: 0.7s 0.4s;
}
.contact ul .contact-item:nth-of-type(2) {
        -webkit-transition: 0.7s 0.8s;
        transition: 0.7s 0.8s;
}
.contact ul .contact-item:nth-of-type(3) {
        -webkit-transition: 0.7s 1.2s;
        transition: 0.7s 1.2s;
}
.contact ul .contact-item:nth-of-type(4) {
        -webkit-transition: 0.7s 1.6s;
        transition: 0.7s 1.6s;
}
.contact ul .contact-item .contact-icon {
        width: 40px;
        height: 40px;
}
.contact ul .contact-item .alert {
        position: absolute;
        bottom: 55px;
        background: skyblue;
        border: 2px solid skyblue;
        border-radius: 4px;
}
.contact ul .contact-item .alert .contact-pic {
          width: 100%;
          height: 100%;
}
.contact ul .contact-item .alert p {
          font-size: 14px;
          margin: 5px;
}
.contact ul .contact-item .alert .triangle {
          position: absolute;
          bottom: -14px;
          content: '';
          width: 0;
          height: 0;
          border-right: 7px solid transparent;
          border-left: 7px solid transparent;
          border-top: 14px solid skyblue;
}
.contact ul .contact-item .fade-enter-active, .contact ul .contact-item .fade-leave-active {
        -webkit-transition: 1s;
        transition: 1s;
}
.contact ul .contact-item .fade-enter, .contact ul .contact-item .fade-leave-to {
        opacity: 0;
}
.contact ul .bounce-enter-active, .contact ul .bounce-leave-ative {
      -webkit-transition: 1s;
      transition: 1s;
      -webkit-transform: translateY(0);
              transform: translateY(0);
}
.contact ul .bounce-enter, .contact ul .bounce-leave-to {
      -webkit-transform: translateY(300px);
              transform: translateY(300px);
      opacity: 0;
}
.contact ul .bounce-leave-active {
      -webkit-transition: 500ms 0s !important;
      transition: 500ms 0s !important;
}
@media screen and (max-width: 320px) {
.motto, .text {
    width: 200px;
    margin-top: 20px;
}
.motto ul .motto-item {
    font-size: 18px;
}
.text .text-item {
    font-size: 14px;
    margin: 10px 0;
}
.contact {
    width: 160px;
    margin: 20px auto 0;
}
.contact ul .contact-item {
      width: 30px;
      height: 30px;
}
.contact ul .contact-item .contact-icon {
        width: 30px;
        height: 30px;
}
}
@media screen and (min-width: 321px) and (max-width: 460px) {
.motto, .text {
    width: 250px;
    margin-top: 22px;
}
.motto ul .motto-item {
    font-size: 19px;
}
.text .text-item {
    font-size: 16px;
    margin: 11px 0;
}
.contact {
    width: 200px;
    margin: 22px auto 0;
}
.contact ul .contact-item {
      width: 32px;
      height: 32px;
}
.contact ul .contact-item .contact-icon {
        width: 32px;
        height: 32px;
}
}
@media screen and (min-width: 461px) and (max-width: 640px) {
.motto, .text {
    width: 300px;
    margin-top: 27px;
}
.motto ul .motto-item {
    font-size: 20px;
}
.text .text-item {
    font-size: 17px;
    margin: 12px 0;
}
.contact {
    width: 250px;
    margin: 27px auto 0;
}
.contact ul .contact-item {
      width: 33px;
      height: 33px;
}
.contact ul .contact-item .contact-icon {
        width: 33px;
        height: 33px;
}
}
@media screen and (min-width: 641px) and (max-width: 900px) {
.motto, .text {
    width: 400px;
    margin-top: 30px;
}
.motto ul .motto-item {
    font-size: 22px;
}
.text .text-item {
    font-size: 18px;
    margin: 15px 0;
}
.contact {
    width: 290px;
    margin: 30px auto 0;
}
.contact ul .contact-item {
      width: 35px;
      height: 35px;
}
.contact ul .contact-item .contact-icon {
        width: 35px;
        height: 35px;
}
}



