
.section {
  overflow: hidden;
}
.photo, .slogan, .introduce {
  width: 100%;
  margin: 0 auto;
  text-align: center;
}
.photo {
  -webkit-transition: 1s;
  transition: 1s;
}
.photo img {
    border: 5px solid rgba(255, 255, 255, 0.9);
    border-radius: 50%;
    margin-top: 200px;
}
.slogan {
  margin-top: 50px;
  -webkit-transition: 1s 200ms;
  transition: 1s 200ms;
}
.slogan .text {
    position: relative;
    font-size: 30px;
    font-weight: 400;
}
.slogan .text .text-left {
      position: absolute;
      top: -20px;
      left: -50px;
}
.slogan .text .text-right {
      position: absolute;
      bottom: -20px;
      right: -50px;
}
.introduce p {
  margin: 40px 0;
  font-size: 23px;
}
.introduce p:first-of-type {
    -webkit-transition: 1s 800ms;
    transition: 1s 800ms;
}
.introduce p:last-of-type {
    -webkit-transition: 1s 900ms;
    transition: 1s 900ms;
}
.photo-move-enter-active, .photo-move-leave-active {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}
.photo-move-leave-active {
  -webkit-transition: 1s !important;
  transition: 1s !important;
}
.photo-move-enter, .photo-move-leave-to {
  -webkit-transform: translateY(200px);
          transform: translateY(200px);
  opacity: 0;
}
@media screen and (max-width: 460px) {
.photo img {
    width: 100px;
    height: 100px;
    margin-top: 100px;
}
.slogan .text {
    font-size: 16px;
}
.slogan .text .text-left {
      top: -10px;
      left: -20px;
}
.slogan .text .text-right {
      bottom: -10px;
      right: -20px;
}
.introduce {
    margin-top: 30px;
}
.introduce p {
      font-size: 14px;
      margin: 15px 0;
}
}
@media screen and (min-width: 461px) and (max-width: 640px) {
.photo img {
    width: 120px;
    height: 120px;
    margin-top: 110px;
}
.slogan .text {
    font-size: 20px;
}
.slogan .text .text-left {
      top: -12px;
      left: -30px;
}
.slogan .text .text-right {
      bottom: -12px;
      right: -30px;
}
.introduce {
    margin-top: 35px;
}
.introduce p {
      font-size: 17px;
      margin: 17px 0;
}
}
@media screen and (min-width: 641px) and (max-width: 900px) {
.photo img {
    width: 140px;
    height: 140px;
    margin-top: 120px;
}
.slogan .text {
    font-size: 25px;
}
.slogan .text .text-left {
      top: -12px;
      left: -40px;
}
.slogan .text .text-right {
      bottom: -12px;
      right: -40px;
}
.introduce {
    margin-top: 50px;
}
.introduce p {
      font-size: 20px;
      margin: 22px 0;
}
}



