
.name, .progress, .percentage {
  float: left;
  vertical-align: middle;
}
.name {
  text-align: right;
  -webkit-transition: 2s;
  transition: 2s;
}
.progress {
  height: 100%;
  margin: 0 12px;
  border-radius: 4px;
  -webkit-transition: 1.5s;
  transition: 1.5s;
}



