
.content {
  position: relative;
  top: 80px;
  width: 1000px;
  height: 100%;
  margin: 0 auto;
}
.content .histogram-wrap, .content .skill {
    position: absolute;
    top: 0;
}
.content .histogram-wrap {
    left: 0;
    width: 100%;
}
.content .histogram-wrap .histogram {
      margin-bottom: 15px;
}
.content .skill {
    right: 0;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    -webkit-animation-delay: 0.5s;
            animation-delay: 0.5s;
}
.content .skill .text li {
      margin: 25px 0;
}
@media screen and (max-width: 320px) {
.content {
    top: 15px;
    width: 100%;
}
.content .histogram-wrap, .content .skill {
      position: static;
}
.content .histogram-wrap .histogram {
      margin-bottom: 7px;
}
.content .histogram-wrap .histogram .wrap {
        width: 91%;
        margin: 0 auto;
}
.content .skill {
      padding: 1px 20px 0;
}
.content .skill .text li {
        margin: 10px 0;
        font-size: 12px;
}
}
@media screen and (min-width: 321px) and (max-width: 460px) {
.content {
    top: 30px;
    width: 100%;
}
.content .histogram-wrap, .content .skill {
      position: static;
}
.content .histogram-wrap .histogram {
      margin-bottom: 10px;
}
.content .histogram-wrap .histogram .wrap {
        width: 300px;
        margin: 0 auto;
}
.content .skill {
      width: 300px;
      margin: 35px auto 0;
}
.content .skill .text li {
        margin: 15px 0;
        font-size: 13px;
}
}
@media screen and (min-width: 461px) and (max-width: 640px) {
.content {
    top: 30px;
    width: 100%;
}
.content .histogram-wrap, .content .skill {
      position: static;
}
.content .histogram-wrap .histogram {
      margin-bottom: 10px;
}
.content .histogram-wrap .histogram .wrap {
        width: 400px;
        margin: 0 auto;
}
.content .skill {
      width: 400px;
      margin: 35px auto 0;
}
.content .skill .text li {
        margin: 15px 0;
        font-size: 15px;
}
}
@media screen and (min-width: 641px) and (max-width: 900px) {
.content {
    top: 40px;
    width: 100%;
}
.content .histogram-wrap, .content .skill {
      position: static;
}
.content .histogram-wrap .histogram {
      margin-bottom: 10px;
}
.content .histogram-wrap .histogram .wrap {
        width: 550px;
        margin: 0 auto;
}
.content .skill {
      width: 500px;
      margin: 35px auto 0;
}
.content .skill .text li {
        margin: 15px 0;
        font-size: 15px;
}
}
@media screen and (min-width: 901px) and (max-width: 1200px) {
.content {
    top: 50px;
    width: 850px;
}
.content .skill {
      width: 400px;
}
.content .skill .text li {
        margin: 20px 0;
}
}



