
body {
  font-family: Helvetica, Tahoma, Arial, "Heiti SC", "Microsoft YaHei", "WenQuanYi Micro Hei";
  color: #fff;
}
#menu {
  position: fixed;
  top: 0;
  width: 100%;
  background: rgba(0, 0, 0, 0.1);
}
#menu ul {
    float: right;
    margin: 22px;
}
#menu .nav-move-enter-active, #menu .nav-move-leave-active {
    -webkit-transition: 1.5s;
    transition: 1.5s;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
#menu .nav-move-enter, #menu .nav-move-leave-to {
    -webkit-transform: translateX(200px);
            transform: translateX(200px);
    opacity: 0;
}
#menu li {
    float: left;
    margin-left: 20px;
}
#menu li a {
      color: #fff;
      text-shadow: 2px 2px 4px #000;
}
#menu .resume-avatar {
    position: absolute;
    top: 50%;
    left: 15px;
    margin-top: -20px;
}
#menu .resume-avatar img {
      border-radius: 50%;
      vertical-align: middle;
}
#menu .resume-avatar span {
      font-size: 14px;
      vertical-align: middle;
      margin-left: 10px;
}
#menu .resume-avatar-move-enter-active, #menu .resume-avatar-move-leave-active {
    -webkit-transition: 1s;
    transition: 1s;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
#menu .resume-avatar-move-enter, #menu .resume-avatar-move-leave-to {
    -webkit-transform: translateX(-200px);
            transform: translateX(-200px);
    opacity: 0;
}
@media screen and (max-width: 460px) {
.right {
    display: none;
}
#menu ul {
    margin: 10px;
}
#menu li {
    margin-left: 10px;
    font-size: 14px;
}
#menu .resume-avatar {
    margin-top: -12px;
}
#menu .resume-avatar .text {
      display: none;
}
#menu .resume-avatar img {
      width: 24px;
      height: 24px;
}
}
@media screen and (min-width: 461px) and (max-width: 640px) {
#menu ul {
    margin: 12px;
}
#menu li {
    margin-left: 10px;
    font-size: 15px;
}
#menu .resume-avatar {
    margin-top: -12px;
}
#menu .resume-avatar img {
      width: 24px;
      height: 24px;
}
}



